.stopwatch {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;

  &__center {
    position: absolute;
    width: 5vmin;
    height: 5vmin;
    background-color: #f6a603;
    border-radius: 50%;
    z-index: 2;
  }

  &__minute-hand {
    position: absolute;
    width: 3vmin;
    height: 20vmin;
    top: 20vmin;
    background-color: #0700ff;
    transform-origin: bottom;
    animation: rotate 3600s steps(60) infinite;
    z-index: 0;
  }

  &__second-hand {
    position: absolute;
    width: 1.5vmin;
    height: 38vmin;
    top: 2vmin;
    background-color: #2c8000;
    transform-origin: bottom;
    animation: rotate 60s linear infinite;
    z-index: 1;
  }

  &--speed-up {
    .stopwatch__second-hand {
      animation-duration: 10s;
    }

    .stopwatch__minute-hand {
      animation-duration: 600s;
    }
  }
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
